import React, { Component } from 'react';
import CategoryServices from '../Services/CategoryServices';
import ProductServices from "../Services/ProductServices"
//import { Img } from 'react-image'
//import { Form, Modal, Button, Header, Dropdown } from "semantic-ui-react";
import Navigation from "../Navbar/index"

class UploadFile extends Component {
    constructor(props) {
        super(props)

        this.state = {
            productName: '',
            productImage: null,
            file: null,
            imageInfos: [],

        }
        this.changeProductNameHandler = this.changeProductNameHandler.bind(this);
        this.changeImageUrlHandler = this.changeImageUrlHandler.bind(this);
        this.cancel = this.cancel.bind(this);
        this.getTitle = this.getTitle.bind(this);
        this.changeFileUpload = this.changeFileUpload.bind(this);
    }

    cancel() {
        this.props.history.push('/products/-1')
    }

    getTitle() {
        return <h3 className="text-center">Upload Product</h3>
    }

    componentDidMount() {
        ProductServices.getProductFile("pqc").then((res) => {

            this.setState({ file: res.data });
            console.log(JSON.stringify(res.data))
            // let blob=JSON.stringify(res.data)
            //var image = new File([JSON.stringify(res.data)], "image.jpg");
            //var binaryData = [];
            //binaryData.push(res.data);
           // const url= window.URL.createObjectURL(new Blob(binaryData, { type: "image/jpeg" }))
            //this.setState({ file:url})
        })
    }
    blobToImage = (file) => {
        var binaryData = [];
        binaryData.push(file);
        return new Promise(resolve => {
            //const url = URL.createObjectURL(blob)
            const url= window.URL.createObjectURL(new Blob(binaryData, { type: "image/jpeg" }))
            let img = new Image()
            img.onload = () => {
                URL.revokeObjectURL(url)
                resolve(img)
            }
            img.src = url
        })
    }


    changeProductNameHandler = (event) => {
        this.setState({ productName: event.target.value });
    }

    changeImageUrlHandler = (event) => {
        this.setState({ productImage: event.target.files[0] })
        console.log(this.state.productImage)
        // console.log(this.blobToImage(this.state.productImage))
        /*  const formData = new FormData()
          formData.append(
              'file',
              this.state.productImage);
          console.log(JSON.stringify(formData));
          /*
                 
                 // let image = event.target.files[0];
                  const formData = new FormData();
                  formData.append('image', image);
                  this.setState({ productImage: formData });
                  console.log(JSON.stringify(formData));*/
    }

    changeFileUpload() {
        const formData = new FormData()
        formData.append(
            "file",
            this.state.productImage,
            this.state.productImage.name

        );

        console.log(JSON.stringify(formData));
        console.log(JSON.stringify(formData) + URL.createObjectURL(this.state.productImage));

        //let imageData={productName:this.state.productName, productImage:this.state.productImage}
        ProductServices.addProductFile(this.state.productName, formData).then(res => {
            this.props.history.push(`/all-category`);
        });
    }


    render() {

        return (
            <div>
                <Navigation />
                <div className="container">
                    <div className="row">
                        <div className="card col-md-6 offset-md-3 offset-md-3">
                            {
                                this.getTitle()
                            }
                            <div className="card-body">
                                <form>
                                    <div className="form-group">
                                        <label><b>Product Name : </b></label>
                                        <input placeholder="Product Name" name="productName" className="form-control" value={this.state.productName} onChange={this.changeProductNameHandler} />
                                    </div>
                                    <div className="form-group"  >
                                        <label><b>Choose images :</b> </label>
                                        <input type="file" onChange={this.changeImageUrlHandler} />
                                    </div>
                                    <button type="submit" className="btn btn-success" onClick={this.changeFileUpload} >Save</button>
                                    <button className="btn btn-dager" onClick={this.cancel.bind(this)} style={{ marginLeft: "10px" }}>Cancel</button>

                                    <div className="card mt-3">
                                        <ul className="list-group list-group-flush">

                                            <img
                                                className="img-thumbnail rounded-circle mx-auto mb-2 shadow-sm"
                                                src={this.blobToImage(this.state.file)}

                                                style={{ width: "200px", height: "200px" }}
                                            />

                                        </ul>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default UploadFile;